import React from "react";
import { Link } from "react-router-dom";
import Firebase from "firebase";

function Footer(props) {
  function HandleClickBack(e) {
    if (e.target.name === "back") {
      props.history.goBack();
    } else {
      props.CreateBackup();
      Firebase.database()
        .ref(props.currentBuild)
        .remove();
      window.location.reload();
    }
  }

  function CalcTotalWeek(week) {
    let sum = 0;
    for (let day in week.days) {
      sum = sum + parseFloat(Object.values(week.days[day])[0]);
    }

    return sum;
  }

  function CalcTotalAllActiveWeeks(worker) {
    let sum = 0;
    if (typeof worker.places !== "undefined") {
      worker.places.forEach(place => {
        sum = sum + CalcTotal(place);
      });
    }

    return sum;
  }

  function CalcTotal(place) {
    if (typeof place.weeks !== "undefined") {
      let sum = 0;
      place.weeks.forEach(week => {
        sum = sum + CalcTotalWeek(week);
      });
      // sum.toFixed(1)
      return sum;
    }

    return 0;
  }

  // Linebreak  %0D%0A
  // space      %20
  //

  function FormatBody() {
    let body = "";

    props.workers.forEach(w => {
      let workerTitle = `${w.name.toUpperCase()} -------------------------------------------------------------------------------------------------------->%0D%0A%0D%0A`;
      w.places.forEach(p => {
        let place = `     ---------------- ${p.name} ----------------%0D%0A`;
        workerTitle = workerTitle.concat(place);

        p.weeks.forEach(v => {
          let week = `%0D%0A    ${v.name} %0D%0A %0D%0A`;
          workerTitle = workerTitle.concat(week);

          v.days.forEach(d => {
            let day = `                 ${Object.keys(d)[0]}: ${
              Object.values(d)[0]
            }%0D%0A`;
            workerTitle = workerTitle.concat(day);
          });

          let yht = `%0D%0A    Yhteensä: ${CalcTotalWeek(v)}%0D%0A%0D%0A`;
          workerTitle = workerTitle.concat(yht);
        });
      });
      let total = `${w.name} kaikki yhteensä: ${CalcTotalAllActiveWeeks(
        w
      )} tuntia%0D%0A %0D%0A`;
      workerTitle = workerTitle.concat(total);
      body = body.concat("%0D%0A", workerTitle);
    });

    return body;
  }

  function HandleClickEmail(e) {
    console.log("Opening mail...");

    const emailTo = "Helin.pool@gmail.com";
    const emailSubject = "Tunnit";
    const emailBody = FormatBody();

    const newUrl =
      "mailto:" + emailTo + "?subject=" + emailSubject + "&body=" + emailBody;
    // "https://mail.google.com/mail?view=cm&tf=0" +
    // (emailTo ? "&to=" + emailTo : "") +
    // (emailSubject ? "&su=" + emailSubject : "") +
    // (emailBody ? "&body=" + emailBody : "");

    console.log(emailBody);
    window.open(newUrl);
  }

  return (
    <div className="Footer">
      {props.location.pathname !== "/" ? (
        <button className="BackButton" name="back" onClick={HandleClickBack}>
          Back
        </button>
      ) : (
        ""
      )}

      <button className="MailButton" onClick={HandleClickEmail}>
        Mail
      </button>
    </div>
  );
}

export default Footer;
